import React, { Component } from 'react';
import styled from 'styled-components';

import { FaPlayCircle, FaPauseCircle } from 'react-icons/fa';

const TimebarWrapper = styled.div`
	font-size: 1.6rem;
	text-transform: uppercase;
	letter-spacing: 1px;
	display: flex;
	.time {
		display: inline;
		margin: 0;
		line-height: 2.4rem;
	}
	.seconds_s {
		text-transform: lowercase;
	}
	.toggle_time {
		cursor: pointer;
		transition: 300ms ease;
		margin-left: 4rem;
		font-size: 2.4rem;
		display: inline-block;
		&:hover {
			color: #d02c2c;
		}
	}
`;

class timebar extends Component {
	handleTimerToggle = () => {
		if (this.props.timerIsPaused) {
			this.props.startTimer();
		} else {
			this.props.stopTimer();
		}
	};

	render() {
		return (
			<TimebarWrapper>
				<p className="time">
					Time Remaining: {this.props.timer}
					<span className="seconds_s">s</span>
				</p>
				{/* inline if statement needed to determine what icon to show */}
				<div onClick={this.handleTimerToggle} className="toggle_time">
					{this.props.timerIsPaused ? <FaPlayCircle /> : <FaPauseCircle />}
				</div>
			</TimebarWrapper>
		);
	}
}

export default timebar;
